.tables-grid {
    display: flex;
    flex-wrap: wrap;
}

.tables-card {
    flex-basis: 20%;
    flex-grow: 1;
    padding: 0px 5px;
}

#tableGrid.card {
    background-color: #F8F8F4;
    border-radius: 40px;
    position: relative;
    margin-bottom: 10px;
}

#tableGrid.badge {
    position: absolute;
    top: 12px;
    right:12px;
    border-radius: 10px 25px 10px 10px;
    font-size: small;
}

#tableGrid.alert {
    margin-bottom: 0px;
    text-align: center;
}

#statusWithFinishButton {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#finishButton {
    border: light;
}